.input-group {
  width: 100%;
}

input[type=text] {
  border: none;
  border-bottom: 2px solid #e67e22;
  background: none;
  outline: #34495e;
  color: #ecf0f1; 
}

input[type=text]:focus {
  background: #34495e;
  border: none;
  border-bottom: 2px solid #e67e22;
  color: #ecf0f1;
  outline: red;
}
.form-control:focus {
  border-color: inherit;
  -webkit-box-shadow: none;
  box-shadow: none;
}

input:focus {
  border:none;
}

button{
  border-radius: 10px;
  background: none;
  color: #ecf0f1;
  outline: none;
  border-style: inset;
}

.btn:focus {
  border-color: #34495e;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.contain {
  background: #34495e;
  border-radius : 10px;
  width: 30%;
  text-align: left;
  padding: 20px;
  margin-top: 5%;
}

.contain__info {
  padding-top: 0px;
}

h3 {
  color: #ecf0f1;
}

span {
  color: #e67e22;
}

.error {
  color: #ecf0f1;
}


